___CSS_LOADER_EXPORT___.push([module.id, "\nbody, html {\n  height: 100%;\n}\n#anon-app {\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n}\n.main-pane {\n  flex: 1;\n  display: flex;\n  flex-direction: row;\n  overflow: hidden;\n}\n.content-pane {\n  flex: 2;\n  overflow-y: auto;\n  padding: 0.5em;\n}\n.sidebar-pane {\n  flex: 1;\n  overflow-y: auto;\n  padding: 0.5em;\n}\n#anon-app mark {\n  background-color: rgba(255, 165, 0, 0.4);\n  padding: 0px;\n  color: inherit;\n}\n#anon-app mark.is-active {\n  background-color: rgba(255, 165, 0, 1.0);\n}\n#anon-app mark.applied {\n  background-color: #aeffae;\n}\n#anon-app mark.applied.is-active {\n  background-color: #00ff00;\n}\n.list-group-item.replacement.is-active {\n  background-color: rgba(255, 165, 0, 0.25);\n}\n.list-group-item.replacement.applied {\n  background-color: #aeffae;\n}\n.list-group-item.replacement.applied.is-active {\n  background-color: #00ff00;\n}\n.content-pane mark {\n  scroll-margin-top: 5em;\n}\n", "",{"version":3,"sources":["webpack://./peachjam/js/components/Anon/AnonApp.vue"],"names":[],"mappings":";AAoFA;EACE,YAAY;AACd;AAEA;EACE,YAAY;EACZ,aAAa;EACb,sBAAsB;AACxB;AAEA;EACE,OAAO;EACP,aAAa;EACb,mBAAmB;EACnB,gBAAgB;AAClB;AAEA;EACE,OAAO;EACP,gBAAgB;EAChB,cAAc;AAChB;AAEA;EACE,OAAO;EACP,gBAAgB;EAChB,cAAc;AAChB;AAEA;EACE,wCAAwC;EACxC,YAAY;EACZ,cAAc;AAChB;AAEA;EACE,wCAAwC;AAC1C;AAEA;EACE,yBAAyB;AAC3B;AAEA;EACE,yBAAyB;AAC3B;AAEA;EACE,yCAAyC;AAC3C;AAEA;EACE,yBAAyB;AAC3B;AAEA;EACE,yBAAyB;AAC3B;AAEA;EACE,sBAAsB;AACxB","sourcesContent":["<template>\n  <header class=\"bg-light p-2\">\n    <div class=\"d-flex mb-2\">\n      <h5 class=\"mb-0\">\n        \n        <a :href=\"`/admin/peachjam/judgment/${documentId}/change/`\">{{ title }}</a>\n      </h5>\n      <button class=\"btn btn-success ms-auto\" :disabled=\"saving\" @click=\"savePublish\">Save and publish</button>\n      <button class=\"btn btn-outline-success ms-2\" :disabled=\"saving\" @click=\"saveDraft\">Save draft</button>\n    </div>\n    <input v-model=\"newCaseName\" class=\"form-control\" />\n  </header>\n  <div class=\"main-pane\">\n    <div class=\"content-pane\">\n      <div ref=\"contentRoot\" id=\"content-root\">\n        <!-- Note: we have two roots, because targets need a root element with an ID that is inside \"content-root\" -->\n        <div ref=\"documentRoot\" id=\"document-root\" v-html=\"contentHtml\" />\n      </div>\n    </div>\n    <div class=\"sidebar-pane border-start\">\n      <ReplacementsPane ref=\"replacements\" :replacements=\"replacements\" :document-id=\"documentId\" />\n    </div>\n  </div>\n</template>\n\n<script>\nimport ReplacementsPane from './ReplacementsPane.vue';\nimport { unwrap } from './replacements';\n\nexport default {\n  components: { ReplacementsPane },\n  props: ['documentId', 'title', 'caseName'],\n  data (self) {\n    return {\n      replacements: [],\n      saving: false,\n      newCaseName: self.caseName,\n      contentHtml: document.getElementById('document-content').innerHTML\n    };\n  },\n  methods: {\n    saveDraft () {\n      this.save(false);\n    },\n    savePublish () {\n      this.save(true);\n    },\n    async save (published) {\n      const root = this.$refs.documentRoot.cloneNode(true);\n      // remove marks\n      for (const mark of root.querySelectorAll('mark')) {\n        unwrap(mark);\n      }\n\n      const html = root.innerHTML;\n      const replacements = this.replacements.filter(r => r.applied).map(r => r.serialise());\n\n      this.saving = true;\n      try {\n        const resp = await fetch(`/admin/anon/${this.documentId}/update`, {\n          method: 'PUT',\n          headers: {\n            'Content-Type': 'application/json',\n            'X-CSRFToken': document.querySelector('input[name=csrfmiddlewaretoken]').value\n          },\n          body: JSON.stringify({\n            case_name: this.newCaseName,\n            content_html: html,\n            published,\n            replacements\n          })\n        });\n        if (!resp.ok) {\n          alert(`Failed to save: ${resp.statusText}`);\n        }\n      } finally {\n        this.saving = false;\n      }\n    }\n  }\n};\n</script>\n\n<style>\nbody, html {\n  height: 100%;\n}\n\n#anon-app {\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n}\n\n.main-pane {\n  flex: 1;\n  display: flex;\n  flex-direction: row;\n  overflow: hidden;\n}\n\n.content-pane {\n  flex: 2;\n  overflow-y: auto;\n  padding: 0.5em;\n}\n\n.sidebar-pane {\n  flex: 1;\n  overflow-y: auto;\n  padding: 0.5em;\n}\n\n#anon-app mark {\n  background-color: rgba(255, 165, 0, 0.4);\n  padding: 0px;\n  color: inherit;\n}\n\n#anon-app mark.is-active {\n  background-color: rgba(255, 165, 0, 1.0);\n}\n\n#anon-app mark.applied {\n  background-color: #aeffae;\n}\n\n#anon-app mark.applied.is-active {\n  background-color: #00ff00;\n}\n\n.list-group-item.replacement.is-active {\n  background-color: rgba(255, 165, 0, 0.25);\n}\n\n.list-group-item.replacement.applied {\n  background-color: #aeffae;\n}\n\n.list-group-item.replacement.applied.is-active {\n  background-color: #00ff00;\n}\n\n.content-pane mark {\n  scroll-margin-top: 5em;\n}\n</style>\n"],"sourceRoot":""}]);
